//Google fonts
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700,800&display=swap");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap");

//Font Variables
$montserrat: "Montserrat", sans-serif;
$raleway: "Raleway", sans-serif;
.montserrat {
  font-family: $montserrat;
}
.raleway {
  font-family: $raleway;
}
// Button Color
$hover-grey: #313131;
$green-btn-color: #4adea0;
$white: #ffffff;
$grey: #2b2b2b;
$grey-bg: #222222;
$grey-header: #2b2b2b;
$text-grey: #939393;
$red: #ff5c5c;
$errorMsg: #a81e1e;
$w-100: 100%;
$w-75: 75%;
$w-50: 50%;
$w-25: 25%;

//Easy use class

.w-100 {
  width: $w-100;
}
.w-100px {
  width: 100px;
}

.w-75 {
  width: $w-75;
}
.w-50 {
  width: $w-50;
}
.w-25 {
  width: $w-25;
}
.m-b-50px {
  margin-bottom: 50px;
}
.text-white {
  color: $white;
}
.text-grey {
  color: $text-grey;
}
.m-r-15 {
  margin-right: 15px;
}
.m-l-15 {
  margin-left: 15px;
}
.m-t-15 {
  margin-top: 15px;
}
.m-b-5 {
  margin-bottom: 5px;
}
.m-b-20 {
  margin-bottom: 20px;
}
.m-t-50 {
  margin-top: 50px !important;
}
.f-right {
  float: right;
}
.text-small {
  font-size: 12px;
}
// Click

.click {
  cursor: pointer;
}

.text-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
