.dashboard-header {
  background: $grey-header;
  height: 92px;
  -webkit-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
  position: relative;
  .browser-item {
    float: left;
    min-width: 455px;
  }
  .menu-icon {
    color: $text-grey;
    font-size: 24px;
  }
  .content {
    position: absolute;
    left: 0;
    right: 0;
    display: inline-table;
    width: auto;
    max-width: 100%;
  }
  .down-icon {
    margin-left: 15px;
  }

  .photo-icon {
    border-radius: 50px;
    max-width: 30px;
    cursor: pointer;
  }

  .user-avatar {
    border-left: 0px solid $white;
    position: relative;
    &:before {
      content: "";
      height: 50px;
      width: 1px;
      background: #fff;
      display: inline-block;
      position: absolute;
      left: 0;
      top: 20px;
    }
  }

  ul {
    padding: 0;

    li {
      min-width: 80px;
      float: left;
      padding: 32px 30px;
      list-style: none;
      text-align: center;

      img {
        max-width: 20px;
      }

      button {
        background: transparent;
        border: 0;
        width: 35px;
        &:focus {
          outline: unset;
        }
      }
    }

    &.first-menu {
      li {
        position: relative;
        &.active {
          background: $grey-bg;
        }
      }
    }
    &.second-menu {
      li {
        position: relative;
        &.active {
          background: $grey-bg;
        }
      }
    }

    &.third-menu {
      li {
        text-align: center;
        display: block;
        margin: auto;
        float: none;
        &.active {
          background: $grey-bg;
        }
      }
    }

    &.fourth-menu {
      li {
        float: right;
        &.active {
          background: $grey-bg;
        }
      }
    }
  }

  .organisationVisible {
    background: #2b2b2b;
    padding: 30px 50px;
    margin-left: 5px;
    position: relative;
    top: 0px;
    height: calc(100vh - 115px);
    overflow: auto;
    max-width: 455px;

    .org_browser {
      position: relative;

      .org-selector {
        position: relative;
        margin-top: 40px;

        h3 {
          font-size: 25px;
          font-weight: normal;
          margin-bottom: 30px;
        }

        input {
          width: 100%;
          font-weight: 500;
          padding: 5px;
          border: 0;
          margin-right: 10px;
          background: transparent;
          border-bottom: 1px solid $text-grey;
        }

        .search-icon {
          position: absolute;
          bottom: 5px;
          left: 50%;
          font-size: 25px;
          color: $white;
        }
      }

      .division {
        margin-top: 40px;

        ul {
          li {
            height: 25px;
            border: 1px solid $text-grey;
            padding: 1px;
            min-width: 0;
            width: 29%;
            margin-right: 10px;
            margin-bottom: 10px;
            border-radius: 15px;
            color: $text-grey;
            font-size: 14px;
            cursor: pointer;

            &:hover {
              background: $white;
              color: $grey;
            }
          }
        }
      }

      .team {
        margin-top: 40px;

        .team-item {
          height: 80px;
          width: 50px;
        }

        .team-container {
          .team-item {
            padding: 10px;
            color: $text-grey;

            .team-img {
              width: 50px;
              height: 50px;
              background: $text-grey;
              border-radius: 100px;
              margin-bottom: 15px;
            }
          }
        }
      }

      .developer {
        margin-top: 40px;

        .developer-item {
          width: 100%;
          text-align: left;
          height: 60px;
          padding: 20px 0;
          border-bottom: 1px solid $text-grey;
          color: $text-grey;

          .developer-img {
            height: 50px;
            width: 50px;
            border: 1px solid;
            border-radius: 100px;
            content: "";
            float: right;
            position: relative;
            bottom: 16px;
          }
        }
      }
    }
  }

  .systemVisible {
    background: $grey;
    padding: 30px 30px;
    margin-left: 5px;
    position: relative;
    top: 0px;
    height: calc(100vh - 115px);
    overflow: auto;
    max-width: 455px;

    h3 {
      font-weight: normal;
    }

    .system-down-icon {
      margin-left: 15px;
      &.left {
        margin: 0;
        position: relative;
        right: 15px;
        min-width: 14px;
        display: inline-block;
      }
    }

    .org_browser {
      position: relative;
      height: 95%;
      .org-selector {
        position: relative;
        margin-top: 40px;

        h3 {
          font-size: 25px;
          font-weight: normal;
          margin-bottom: 30px;
        }

        input {
          width: 100%;
          font-weight: 500;
          padding: 5px;
          border: 0;
          margin-right: 10px;
          background: transparent;
          border-bottom: 1px solid $text-grey;
          color: $text-grey;
          font-family: $montserrat;
        }

        .search-icon {
          position: absolute;
          font-size: 25px;
          color: $white;
        }
      }

      .systems {
        margin-top: 20px;
        position: relative;
        .systemClick {
          i {
            transform: rotate(-90deg);
          }
          &.active {
            i {
              transform: rotate(0deg);
            }
          }
        }

        .extra-add {
          position: relative;
          margin-right: -15px;
          margin-bottom: 15px;
          input {
            &#system {
              border: 0;
              border-bottom: 1px solid $text-grey;
              background: transparent;
              width: 100%;
              margin-right: 0;
            }
          }
          button {
            background: transparent;
            border: 0;
            position: absolute;
            right: 0;
          }
          .fa-times {
            color: $white;
            font-style: 20px;
          }
        }
        span {
          color: $white;
        }

        .box {
          margin-left: -15px;
        }

        .custom-dm {
          top: 45px !important;
          background: transparent;
          color: $white;
          line-height: 25px;
          border: 0;
          padding: 0;
          position: relative !important;
        }

        .custom-dp {
          background: transparent;
          border: 0;
          padding: 0;
          position: relative;
          font-weight: normal;
        }
      }

      .flows {
        margin-top: 40px;
        .flowClick {
          i {
            transform: rotate(-90deg);
          }
          &.active {
            i {
              transform: rotate(0deg);
            }
          }
        }
        .extra-add {
