              background: transparent;
              width: 100%;
              margin-right: 0;
            }
          }
          button {
            background: transparent;
            border: 0;
            position: absolute;
            right: 0;
          }
          .fa-times {
            color: $white;
            font-style: 20px;
          }
        }

        .box {
          margin-left: -15px;
        }
      }

      .developer {
        margin-top: 40px;

        .developer-item {
          width: 100%;
          text-align: left;
          height: 60px;
          padding: 20px 0;
          border-bottom: 1px solid $text-grey;

          .developer-img {
            height: 50px;
            width: 50px;
            border: 1px solid;
            border-radius: 100px;
            content: "";
            float: right;
            position: relative;
            bottom: 16px;
          }
        }
      }
    }
  }
