.userVisible {
  background: $grey;
  padding: 30px 50px;
  position: absolute;
  top: 100px;
  overflow: auto;
  min-width: 430px;
  right: 10px;
  text-align: left;
  font-family: $montserrat;
  .usermenu {
    ul {
      li {
        width: 100%;
        float: left;
        text-align: left;
        padding: 15px 0px;
        border-bottom: 1px solid #393939;
        .name {
          margin-left: 15px;
        }
        .caret {
          float: right;
        }
        &.userarea {
          padding: 30px 0px;
        }

        &.no-border {
          border: 0;
          padding: 5px 0;
          .submenu-icon {
            margin-right: 15px;
          }
        }
      }
    }
  }
}
.org_browser {
  height: 95%;
  .browser_close {
    position: absolute;
    right: 0px;
    top: 4px;
    cursor: pointer;
  }
}
.ellipsis-arrow {
  position: absolute;
  right: 0;
  top: 3px;
}
.react-contextmenu {
  width: 200px;
  background: $grey-bg;
  color: $white;
  z-index: 1;
  .react-contextmenu-item {
    padding: 10px 15px;
    border-bottom: 1px solid $grey;
    cursor: pointer;
    &:hover {
      background: $hover-grey;
    }
  }
}
.react-contextmenu-wrapper {
  .click {
    i {
      transform: rotate(-90deg);
    }
    &.active {
      i {
        transform: rotate(0deg);
      }
    }
  }
}
