body {
  font-family: $montserrat;
  scroll-behavior: smooth;
  background: $grey-bg;

  &:-webkit-scrollbar {
    width: 1em;
  }

  &:-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }

  &:-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
  }
}
h1,
h2,
h3,
h4,
h5,
p {
  font-family: $montserrat;
  font-weight: normal !important;
}
//Landing Page Starts Here.

.home-container {
  padding: 0 30px;
  background: url("../images/homeBG-plan.jpg");
  background-size: cover;

  .logo {
    max-width: 100px;
  }

  .home-main {
    height: 100vh;
  }

  .home-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 15px 30px;
    z-index: 1;
  }

  .signWrapper {
    display: block;
    margin: auto 0;
    position: relative;

    .signin {
      font-size: 25px;
      color: $white;
      cursor: pointer;
      font-weight: bold;
    }

    .signincontent {
      background: $grey;
      border-radius: 4px;
      width: 418px;
      margin: 20px auto;
      padding: 35px 45px;
      position: absolute;
      top: 100%;
      right: 0;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
      color: $white;

      &:after,
      &:before {
        bottom: 100%;
        right: 5%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
      }

      &:after {
        border-color: rgba(136, 183, 213, 0);
        border-bottom-color: $grey;
        border-width: 17px;
        margin-left: -30px;
      }

      &:before {
        border-color: rgba(194, 225, 245, 0);
        border-bottom-color: $grey;
        border-width: 17px;
        margin-left: -36px;
      }

      input {
        background: transparent;
        border: 0;
        border-bottom: 1px solid #939393;
        color: #939393;
        padding-left: 0;
        margin-bottom: 35px;
      }

      .btn {
        width: $w-100 !important;
      }

      .formSeperate {
        text-align: center;
        margin-top: 25px;
        position: relative;

        &:before {
          width: 85px;
          background: #444;
          height: 1px;
          content: "";
          position: absolute;
          display: block;
          top: 12px;
          left: 0;
        }

        &:after {
          width: 200px;
          background: #444444;
          height: 1px;
          content: "";
          width: 85px;
          background: #444;
          height: 1px;
          content: "";
          position: absolute;
          display: block;
          top: 12px;
          right: 0;
        }
      }

      .red-btn {
        margin-top: 24px;
      }

      form {
        margin-top: 50px;
      }

      .errorMsg {
        position: relative;
        top: -30px;
        font-size: 12px;
        color: $errorMsg;
      }
    }
  }

  .banner-content {
    z-index: 1;

    h2 {
      color: $white;
      font-size: 54px;
      font-weight: normal;
    }

    .banner-button {
      margin-top: 78px;
    }

    .green-btn {
      margin-right: 25px;
      margin-bottom: 25px;
    }
    .transparent-btn {
      margin-right: 25px;
      margin-bottom: 25px;
    }
  }

  .home-main-container {
    position: relative;

    .home-desktop-top {
      position: absolute;
      bottom: 0;
      right: 0;
      margin: 0 -30px;
    }

    .home-desktop-bottom-container {
      height: 40vh;

      .home-desktop-bottom {
        position: absolute;
        top: 0;
        right: 0;
        margin: 0 -30px;
      }
    }

    .home-desktop-content-container {
      .hd-banner-button {
        display: block;
        margin: auto;
        text-align: center;
        margin-bottom: 150px;
        margin-top: 100px;

        .green-btn {
          margin-right: 30px;
        }
      }

      .hd-item {
        padding: 30px;
      }

      .input-show {
        input {
          height: 57px;
          width: 640px;
          font-weight: 500;
          padding: 15px;
          border-radius: 5px;
          border: 0;
          margin-right: 30px;
        }
        .close-btn {
          background: transparent;
          border: 0;
        }
      }
    }
  }
}

//Landing Page Ends Here.

//Dashboard Page Starts Here.
.dashboard {
  background: $grey-bg;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

//Dashboard Page Ends Here.
._search {
  position: relative;
  z-index: 1;
  cursor: text;
  overflow: hidden;
}
._search .search-icon {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(calc(50% - 10px));
  transform: translateX(calc(50% - 10px));
  -webkit-transition-timing-function: ease;
  transition-timing-function: ease;
  -webkit-transition: all 300ms;
  transition: all 300ms;
  transition-delay: 0s;
  -webkit-transition-delay: 50ms;
  transition-delay: 50ms;
  left: 0;
  font-size: 25px;
  color: $white;
}
._search:hover .search-icon {
  
  
}
.active._search .search-icon {
  -webkit-transform: translateX(90%);
  transform: translateX(90%);
}
.highlight {
  color: $white;
  font-weight: bold;
}

/* === Right Shortcut Buttons === */
.right-btns{
  position: fixed;
  right: 25px;
  top: 150px;
  
  li{
    margin-bottom: 30px;
  }
  li button{
    background: transparent;
    border: 0;
    width: 35px;
    &:focus {
      outline: unset;
    }
  }
  li button span.icon{
    color: #939393;
    font-size: 24px;
  }
}
