.forcegraph{
    display: block;
	background: #222222;
	.link {
		stroke: #fff;
		stroke-opacity: 0.5;
		stroke-width: 0.5px;
	}
	.node{
		circle {
			fill: #fff;
			&.system {
				fill: #28a745;
			}
			&.service {
				fill: #77d3ff;
			}
			&:hover{
				fill: #e8455c;
    			cursor: pointer;
			}
		}
		text {
			font: 10px sans-serif;
			fill: #fff;
			&.service{
				display: none;
			}
		}
		
	} 
	.cell {
		fill: none;
		pointer-events: all;
	}
	.apiline{
		stroke: #fff;
		stroke-opacity: 0.5;
		stroke-width: 1px;
	}
}