.btn {
    height: 57px ;
    width: 271px;
    font-weight: 500;
    padding: 15px;
    &.green-btn {
        background: $green-btn-color;
        color: $white !important;  
        border: 1px solid $green-btn-color;
        border-radius: 5px;
        &:hover {
            background: transparent;
            color: $green-btn-color !important;  
            border: 1px solid $green-btn-color;
            border-radius: 5px;
        }
    }
    &.transparent-btn {
        background: transparent;
        color: $white !important;  
        border: 1px solid $white;
        border-radius: 5px;
        &:hover {
            background: $white;
            color: $grey !important;  
            border: 1px solid $white;
            border-radius: 5px;
        }
    }
    &.red-btn {
        background: $red;
        color: $white;  
        border: 1px solid $red;
        border-radius: 5px;
    }
}